import React from "react"
import { Link, graphql } from "gatsby"
import Header from '../components/header'
import SEO from '../components/seo'
import Post from '../components/post'
import Footer from '../components/footer'
import Tags from '../components/tags'
import '../pages/index.css'

const TagsPage = ({ pageContext, data }) => {
  const { tag } = pageContext
  const { edges } = data.allMarkdownRemark

  return (
    <div className="container">
    <SEO />
    <Header />
    <div className="posts">
      <Tags />
      <h3 style={{ fontSize: '2rem', color: '#f5ab35', margin: '10px 0' }}>#{tag}</h3>
      <div className="content">
        {edges.map(({node}) => (
          <Link to={node.fields.slug}>
            <Post title={node.frontmatter.title} date={node.frontmatter.date} cover={node.frontmatter.image.childImageSharp.fluid} excerpt={node.excerpt} />
          </Link>
        ))}
      </div>
    </div>
    <Footer />
    </div>
  )
}

export default TagsPage

export const pageQuery = graphql`
  query($tag: String) {
    allMarkdownRemark(
      limit: 2000
      sort: { fields: [frontmatter___date], order: DESC }
      filter: { frontmatter: { tags: { in: [$tag] } } }
    ) {
      totalCount
      edges {
        node {
          fields{
            slug
          }
          excerpt(pruneLength: 80)
          frontmatter {
            date(formatString: "MMMM D, YYYY")
            title
            image {
              childImageSharp {
                resize(width: 1500, height: 1500) {
                  src
                }
                fluid(maxWidth: 786) {
                  ...GatsbyImageSharpFluid
                }
              }
           }
          }
        }
      }
    }
  }
`